import BonePreview from './sneak-peak-bone.gif';
import React from 'react';
import ReactDOM from 'react-dom';
import Countdown from 'react-countdown';
import "./countdown.css"


const mintPrice = 80;
const tokenPayment = "MATIC";
const totalSupply = "N/A"
function Mint() {
    const renderer = ({ days, hours, minutes, seconds, completed }) => {
    if (completed) {
      // Render a completed state
      return <span> <br/> <p class="launch-cd">Mint is Live</p></span>;
    } else {
      // Render a countdown
      return <span>{days}d {hours}h {minutes}m {seconds}s <br/> <p class="launch-cd">Until Launch</p></span>;
        }
    }; 
  return (
      
    <div className="mint-cmp">
           {/* Mint */}
           <div class="mint is-rounded nes-container is-centered with-title">
            <span class="title">Mint a Bone</span>
           <h1>COMING SOON</h1>
           
           <Countdown
    date="2021-11-06T06:00:00"
    intervalDelay={0}
    precision={0}
    autoStart="true"
    renderer={renderer}
  />
  
     
    
    <img class="bone-md" src={BonePreview}></img>
    <p>1 BONE = {mintPrice} MATIC</p>
    <p>Total Minted:</p>
    <div class="mint-count">
    <a class="nes-badge">{totalSupply} / 999</a>
    </div>
     <progress class="nes-progress is-rounded" value="0" max="999"></progress>
     <button class="mint-btn nes-btn is-warning">Connect Wallet</button>
     


       <div class="mint-details is-rounded is-centered nes-container with-title">
          <span class="title">Details</span>
          <ul class="mint-price nes-list is-disc">
            <li>Mint Price: {mintPrice} {tokenPayment}</li>
            <li>Total Bones: 999</li>
            <a href="/#home"><li>Verified Contract Address</li></a>
          </ul>
            </div>
            </div>
        </div>
    
  );
}

export default Mint;