
import "./Slider.css"
import Bone1 from './assets/1.png'
import Bone2 from './assets/2.png'
import Bone3 from './assets/3.png'
import Bone4 from './assets/4.png'
import Bone5 from './assets/5.png'
import Bone6 from './assets/6.png'
import React from "react";
import Slider from "react-slick";

export default function SimpleSlider() {
  var settings = {
    dots: false,
    infinite: true,
    speed: 1600,
    lazyLoad: true,
    autoplaySpeed: 1200,
    cssEase: "ease",
    autoplay: true,
    slidesToShow: 3,
    slidesToScroll: 1,
    initialSlide: 3,
    arrows: false,
    pauseOnHover: false,
    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2,
        }
      },
      {
        breakpoint: 600,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          initialSlide: 1
        }
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow:1,
          slidesToScroll: 1
        }
      }
    ]
  };
  return (
    <Slider {...settings}>
      
      <div class="showcase-cnt is-rounded is-centered with-title nes-container">
        <span class="title">BitBone #1664</span>
        <img class="slide-img" src={Bone1} />
      </div>

      <div class="showcase-cnt is-rounded is-centered with-title nes-container">
        <span class="title">BitBone #66</span>
        <img class="slide-img" src={Bone2} />
      </div>

      <div class="showcase-cnt is-rounded is-centered with-title nes-container">
        <span class="title">BitBone #173</span>
        <img class="slide-img" src={Bone3} />
      </div>

      <div class="showcase-cnt is-rounded is-centered with-title nes-container">
        <span class="title">BitBone #3612</span>
        <img class="slide-img" src={Bone4} />
      </div>

      <div class="showcase-cnt is-rounded is-centered with-title nes-container">
        <span class="title">BitBone #404</span>
        <img class="slide-img" src={Bone5} />
      </div>

      <div class="showcase-cnt is-rounded is-centered with-title nes-container">
        <span class="title">BitBone #666</span>
        <img class="slide-img" src={Bone6} />
      </div>
      
    </Slider>
  );
}
